<template>
    <div class="divination">
        <img class="game_return" src="../assets/divinationHome_back.png" @click="backRn" alt="">
        <div :class="styleState == false ? 'div_content' : 'div_content1' " v-if="showInput">
            <img class="divination_cover" src="../assets/divination_divCover.png" alt="">
            <div class="divcover_content">
                <p class="divCover_p1"></p>
                <input type="number" @focus="getFocus" @blur="loseFocus" v-model="phone_input">
                <p class="divCover_p2"><img src="../assets/divination_sureButton.png" alt="" @click="input_sumbit"></p>
            </div>
        </div>
        <div class="div_content" v-if="showError">
            <img class="divination_cover" src="../assets/divination_divCover.png" alt="">
            <div class="divcover_content">
                <p class="divCover_p1">:</p>
                <p class="divCover_p1">(*)</p>
                <p><img src="../assets/divination_sureButton.png" alt="" @click="error_sumit"></p>
            </div>
        </div>
        <div class="div_contentMsg" v-if="showMsg">
            <p class="answerTitle"></p>
            <img class="divination_coverMsg" src="../assets/divination_answerCover.png" alt="">
            <div class="divcover_contentMsg">
                <p class="divCover_Msgp1">{{getAnswer()}}</p>
            </div>
        </div>
        <img class="divination_prev" src="../assets/divnation_prevButton.png" alt="" @click="divination_prev">
    </div>
</template>

<script>
// import axios from 'axios';
import  MessageBox  from 'mint-ui';


export default {
  name: 'divinationHome',
  data () {
    return {
      timeOut:'',//()
      gameReturnTime:null, //
      styleState:false,
      phone_input:'',
      showInput:true,
      showError:false,
      showMsg:false,
      answers:[   
        '',
        '',
        '',
        '',
        '',
    	'',
    	'',
    	'',
    	'',
    	'',
    	'',
    	'',
    	',,,,',
    	',,,',
    	',,,,',
    	',,,,,',
    	'',
    	'',
    	'',
      '',
    'S',
    '',
    '',
    '',
    'Party',
    '',
    '',
    '',
    '',
    '',
    ''
    ],
    }
  },
  created(){
      if(localStorage.getItem('gameReturnTime')){
           this.gameReturnTime = localStorage.getItem('gameReturnTime')
         }else{
           this.gameReturnTime = 60
         }
    this.isTimeOut()//
  },
  destroyed(){
    clearTimeout(this.timeOut)
  },
  methods: {
     //
    startTimer() {
         let that = this;
        clearTimeout(that.timeOut);
        that.timeOut = setTimeout(function () {
          console.log('')//
          that.backRn() //
        },1000*that.gameReturnTime)
    },
    isTimeOut() {
      let that = this;
      if(that.$route.path == "/divination") {//
        that.startTimer();//
      }
    },
      getAnswer() {
        var random = Math.floor(Math.random() * 31); 
        var phoneInfo = JSON.parse(localStorage.getItem("phoneInfo"));
        //localstorage
        if(phoneInfo == null || phoneInfo == ''){
        var phoneInfo = {}
        phoneInfo[this.phone_input] = [new Date().getDay(),random];
        var setData = localStorage.setItem("phoneInfo",JSON.stringify(phoneInfo));
        }else{
          if(phoneInfo[this.phone_input] == null || phoneInfo[this.phone_input] == ''){
          phoneInfo[this.phone_input] = [new Date().getDay(), random];
          var setData = localStorage.setItem("phoneInfo",JSON.stringify(phoneInfo));
          }
        }
        if(phoneInfo[this.phone_input][0] == new Date().getDay())
        {
        random = phoneInfo[this.phone_input][1];
        }
        return this.answers[random];
},
     
      //  
      input_sumbit(){
         this.startTimer()//
          var phoneReg=/^[1][3-9][0-9]{9}$/;
          if(!phoneReg.test(this.phone_input) || this.phone_input == undefined || this.phone_input == ''){
              this.showInput = false
              this.showError = true
              this.phone_input = '';
              window.postMessage('saveErrorSure')
          }else{
               this.showInput = false;
               this.showMsg = true;
            //    console.log(this.phone_input,66)
               window.postMessage('phone'+':'+this.phone_input);
              
          }  
          
      },
      //  
      error_sumit(){
          this.showError = false
          this.showInput = true
          this.startTimer()//
      },
    //
    divination_prev(){
        if( this.showMsg == true && this.showInput == false){
           this.phone_input = '';
           this.showInput = true;
           this.showMsg = false;
           this.startTimer()//
        }else if(this.showMsg == false && this.showInput == true){
            // console.log('')
            this.$router.push({name:'divinationHomedown'})
        }else{
            this.phone_input = '';
           this.showInput = true;
           this.showMsg = false;
           this.showError = false;
           this.startTimer()//
        }
    },
    backRn(){
      window.postMessage('gameHome');
    },
    getFocus(){
        this.styleState = false
    },
    loseFocus(){
        this.styleState = false
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.divination {
  width: 100%;
  height: 100vh;
  background-image: url("../assets/divination_bg.png");
  background-size: 100% 100%;
  position: relative;
}
.divination .game_return {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: auto;
}
.div_content {
  width: 450px;
  height: 232px;
  margin: auto;
  position: absolute;
  top: 150px;
  /* bottom: 0; */
  left: 0;
  right: 0;
}
.div_content1 {
  width: 450px;
  height: 232px;
  margin: auto;
  position: absolute;
  top: 150px;
  left: 0;
  right: 0;
}
.div_contentMsg {
  width: 660px;
  height: 420px;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.answerTitle {
    position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    margin: auto;
    font-family: "SimSun";
    font-size: 36px;
    color: #D8D6AF
}
.divination .divination_cover {
  width: 100%;
  height: 100%;
}
.divination .divination_coverMsg {
  width: 100%;
  height: 100%;
}
.divcover_content {
  width: 400px;
  height: 160px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
}
.divcover_content .divCover_p1 {
  font-size: 23px;
  font-weight: 200;
  color: #ddce9f;
  font-family: "SimSun";
  padding-bottom: 30px;
}
.divcover_contentMsg {
  width: 600px;
  height: 350px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: justify;
}
.divcover_contentMsg .divCover_Msgp1 {
  font-size: 24px;
  font-weight: 200;
  color: #d8d6af;
  font-family: "SimSun";
  line-height: 32px;
}
.divcover_content input {
  width: 204px;
  height: 32px;
  border: 3px solid #756c4e;
  background-color: #170503;
  color: #ddce9f;
  font-size: 20px;
  font-weight: 200;
  font-family: "SimSun";
  padding-left: 5px;
}
.divcover_content .divCover_p2 {
  padding-top: 30px;
}
.divination_prev {
  position: absolute;
  bottom: 20px;
  right: 20px;
}
</style>



